import React from 'react';
import '../styles/Principal.css';

export default class Sobre extends React.Component {
    render() {
        return(
            <div>
                <h2>Sobre mim</h2>
                <p>Graduado em Projetos Mecânicos pela Fatec-SP, trabalho há 5 anos na área.<br />
                    Estou em busca de uma oportunidade para atuar como desenvolvedor web,
                utilizando React/Flask (para front end) ou Python/Node.js (para back end).</p>
            </div>
        );
    }
};



