import React, { Component } from 'react';
import '../styles/Principal.css';
import Picture from './index.png';

export default class Inicio extends Component {
    render() {
        return (
            <div className="background jumbotron App-total">
                <div className="App">
                    <div className="container clearfix">
                        <img src={Picture} alt="Foto" className="App-img" />
                        <h1 id="title" className="App-title">Gabriel Maia Francisco</h1>
                        <h2 className="App-subtitle">Desenhista projetista/<br />Front-end Web Developer</h2>
                        <div className="App-icons">
                            <a href="http://github.com/gabrielmaiaf" target="_blank" rel="noopener noreferrer"><i className="fa fa-github"></i></a>
                            <a href="http://br.linkedin.com/in/gabrielmaiaf" target="_blank" rel="noopener noreferrer"><i className="fa fa-linkedin-square"></i></a>
                            <a href="mailto:gabrielmaiaf@outlook.com"><i className="fa fa-envelope"></i></a>
                        </div>
                    </div>
                </div>
            </div>
        );
    }
};



