import React, { Component } from 'react';

let data = [
    { key:"1", empresa: "PróFrio Refrigeração Industrial", cargo: "Desenhista Projetista", inicio: "Outubro/2012", fim: "agora", descricao: "Desenho e projeto de câmaras frigoríficas, encaminhamento de eletrocalhas e eletrodutos (elétrica), arranjo da câmara frigorífica com painéis de EPS, XPS ou PUR (civil), projeto dimensional de racks, chillers ou unidades condensadoras no Solidworks ou AutoCAD (disposição de equipamentos, encaminhamento de tubulação e projeto de estrutura ou carenagem); dimensionamento de tubulação da unidade condensadora até o evaporador; posicionamento dos componentes e encaminhamento da tubulação; quantificação de materiais a serem usados na execução dos projetos, tanto de racks quanto a câmara frigorífica em si; criação de projetos e gerenciamento de materiais no sistema Protheus (Microsiga); visitas técnicas para detalhamento dos projetos; elaboração de manual técnico da instalação para o cliente, gerenciamento da área de informática, fazendo a manutenção dos computadores em caso de falha, criação e manutenção de e-mails da empresa, gerenciamento do site." },
    { key:"2", empresa: "Indústria Mecânica Favimar", cargo: "Meio Oficial Ferramenteiro", inicio: "Abril/2011", fim: "Setembro/2012", descricao: "Usinagem de estampos, desde a peça bruta até o try-out do produto, envolvendo usinagem em tornos, fresas e eletro-erosão a fio. Manutenção em estampos e auxílio na produção da empresa." },
    { key:"3", empresa: "Grupo Bluestar Net", cargo: "Auxiliar Geral", inicio: "Outubro/2010", fim: "Janeiro/2011", descricao: "Usinagem de moldes plásticos, desde a peça bruta até o try-out do produto." },
    { key:"4", empresa: "Clube Esperia", cargo: "Aprendiz da área Financeira", inicio: "Julho/2010", fim: "Outubro/2010", descricao: "Emissão de boletos, baixa em cheques, contato com clientes sobre cheques devolvidos, etc." },
    { key:"5", empresa: "SABESP - Companhia de Saneamento Básico do estado de São Paulo", cargo: "Estagiário em Técnico em Mecânica", inicio: "Abril/2009", fim: "Dezembro/2009", descricao: "Conhecimento sobre o sistema hídrico de abastecimento da população. Detalhamento de bombas hidráulicas e válvulas. Envolvimento com o sistema de qualidade da empresa, contatando gerentes e supervisores de outras unidades sobre o serviço prestado da empresa. Desenho utilizando o AutoCAD sobre eixos de bombas, dispositivos de içamento, mancais de deslizamento." },
];

export default class Experiencia extends Component {
    render() {
        return(
            <div>
                <div>
                    <h2>Experiência</h2>
                </div>
                <div>
                    {data.map ((data) =>
                        <div key="{data.key}">
                            <h3>{ data.empresa }</h3>
                            <h4>{ data.cargo }</h4>
                            <div><strong>{ data.inicio }</strong> até <strong>{ data.fim }</strong></div>
                            <p>{ data.descricao }</p>
                            <hr />
                        </div>
                    )};
                </div>
            </div>
        );
    }
};



